.navbar{
  display: flex;
  align-items: center;
  position: fixed;  
  color: #ffffff;
  width: 90%;
  height: 60px; 
  padding: 10px;
  font-size: calc(10px + 2vmin);
  
}
.navbar img{
  width: 25px;
  margin: 10px;
}
.header-texto{
  display: flex;
  align-items: center;
  flex-direction: column;
  /* padding: 10px; */

}
/* .imput-box{
  width: 350px;
} */

.main{
  color: rgb(255, 174, 0);
  width: 100%;
  height: 100vh;
  /* background-color:#65038c; */
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  

}
.btns{
  display: flex;
  direction: row;  
  align-items: stretch;
  justify-content: space-evenly;
  flex-wrap: nowrap;
  padding: 15px;
  width: 400px;
}
.texto{
  width: 340px;
  height: 30px;
  border-radius: 5px;
  border: none;
}
.button{
  width: 160px;
  height: 40px;  
  background-color: rgb(255, 174, 0);
  border: none;
  border-radius: 5px;
}
.primario{
  background-color: aliceblue;
}
.primario:hover{
  background-color: rgb(199, 199, 199);
}

.form-group{
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer a{
  color: #ffffff;
  text-decoration: none;  
  position: relative;
}